<template>
    <div class="home">
        <div>
            

            <div v-for="item in Olympic">
            	<h1><strong>{{item.name}} {{item.surname}}</strong> <small>details</small> </h1>
                <div class="md-layout md-gutter">

                    <div class="md-layout-item">
                        <md-card>
                            <md-card-media-cover md-solid>
                                <md-card-media md-ratio="4:3">
                                    <img :src="getImageurl(item.photo.data)" alt="Skyscraper">
                                </md-card-media>

                                <md-card-area>
                                    <md-card-header>
                                        <span class="md-title">{{item.name}} {{item.surname}}</span>
                                        <span class="md-subhead"><p> Dimensions : {{item.weight}}--{{item.height}}</p>
                                        	<p>Birth : {{item.date_of_birth}}</p>
                                        </span>
                                    </md-card-header>

                                    <md-card-actions>
                                        <h3><img width="50%" src="@/assets/gold.png">{{item.gold}}</h3>
                                        <h3><img width="50%" src="@/assets/silver.png">{{item.silver}}</h3>
                                        <h3><img width="50%" src="@/assets/bronze.png">{{item.bronze}}</h3>

                                        <md-button class="md-icon-button">
                                            <md-icon>share</md-icon>
                                        </md-button>
                                    </md-card-actions>
                                </md-card-area>
                            </md-card-media-cover>
                        </md-card>
                    </div>

                    <div class="md-layout-item">
                        <vue-simple-markdown :source="item.bio"></vue-simple-markdown>
                    </div>
                </div>
            </div>

        </div>
        <HelloWorld msg="Welcome to Your Vue.js App" />
    </div>
</template>

<script>
import HelloWorld from '@/components/HelloWorld.vue'
import {
    Carousel,
    Slide
} from 'vue-carousel';
import VuePaginator from 'vuejs-paginator'
import {
    store
} from '../store/index'
export default {
    name: 'Home',
    components: {
        HelloWorld,
        VPaginator: VuePaginator,
        Carousel,
        Slide
    },
    data: () => ({
        Olympic: null,
        Games: null,
        selectedgameid: "Tokyo",
        src: "data:image/gif;base64,R0lGODlhAQABAIAAAAAAAP///yH5BAEAAAAALAAAAAABAAEAAAIBRAA7"
    }),

    methods: {

        printOlympic() {
            const d = this.$store.getters.id
            async function getOlympicId() {

                const response = await fetch("https://immense-ocean-06133.herokuapp.com/" + "athleteID?keywords=" + d);
                return await response.json();
            }
            getOlympicId().then(x => {
                this.Olympic = x
                
            })
        },


        getImageurl(response) {
            const intArray = new Uint8Array(response);
            const reducedArray = intArray.reduce((data, byte) => data + String.fromCharCode(byte), '');

            const base64String = `data:image/png;base64, ${btoa(reducedArray)}`;


            return base64String;

        }

    },
    mounted() {

        this.printOlympic()


    },
    computed: {

        // console.log(this.Olympic)


    }
}
</script>